#header {
    position: fixed;
    z-index: 1000;
    left: 0;
    top: 0;
    width: 100vw;
    height: auto;
    transition: .5s ease background-color;

}


.brand img {
    width: 100px;
}
.header {
    min-height: 3vh;
    background-color: rgba(10, 22, 26, 0.44);
}

.navBar {
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 100%;
    height: 100%;
    max-width: 1300px;
    padding: 0 10px;

}

.navList ul {
    list-style: none;
    position: absolute;
    background-color: rgba(31, 30, 30, 0.24);
    width: 100vw;
    height: 100vh;
    left: 1100px;
    top: 0;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    z-index: 1;
    overflow-x: hidden;
    transition: .9s ease left;

}

.navList ul.active {
    left: 0;
    background-color: #07242f;
}

.navList ul .link {
    font-weight: 500;
    letter-spacing: .2rem;
    text-decoration: none;
    color: white;
    text-transform: uppercase;
    padding: 20px;


}
#header .navList ul .link::after {
    content: attr(data-after);
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%) scale(0);
    color: rgba(33, 87, 134, 0.02);
    font-size: 13rem;
    letter-spacing: 50px;
    z-index: -1;
    transition: .3s ease letter-spacing;

}


.navList ul .link:hover {
  color: #68cff5
}

.navList ul .link:hover::after {
    transform: translate(-50%, -50%) scale(1);
    letter-spacing: initial;

}

.hamburger {
    display: flex;
    height: 60px;
    width: 60px;
    border: 3px solid white;
    border-radius: 50%;
    position: relative;
    align-items: center;
    justify-content: center;
    z-index: 100;
    cursor: pointer;
    transform: scale(.8);
    margin-right: 20px;

}

.hamburger::after {
    content: '';
    position: absolute;
    height: 100%;
    width: 100%;
    border-radius: 50%;
    border: 3px solid white;
    animation: hamburger_pulse 3s ease infinite;

}

.hamburger .bar {
    height: 2px;
    width: 30px;
    position: relative;
    background-color: white;
    z-index: -1;

}

.headerScrolled {
    background-color: #29323c !important;
}

.hamburger .bar::after,
.hamburger .bar::before {
    content: '';
    position: absolute;
    height: 100%;
    width: 100%;

    background-color: white;
    transition: .3s ease;
    transition-property: top, bottom;


}

.hamburger .bar::after {
    top: 8px;
}

.hamburger .bar::before {
    bottom: 8px;
}

.hamburger.active .bar::before {
    bottom: 0;
}

.hamburger.active .bar::after {
    top: 0;
}

@media only screen and (min-width: 1200px) {
    .hamburger {
        display: none;

    }

    .navList ul.active {
        background-color: transparent;
    }

    .navList ul {
        position: initial;
        display: block;
        height: 70px;
        width: fit-content;
        background-color: transparent;
    }

    .navList .link {
        display: inline-block;
    }

    .navList .link {
        font-size: 1rem;
        transition: .5s ease;

    }

    .navList .link:after {
        display: none;
    }
}

@keyframes hamburger_pulse {
    0% {
        opacity: 1;
        transform: scale(1);
    }
    100% {
        opacity: 0;
        transform: scale(1.4);
    }
}